.apropos-container {
  display: flex;
  width: 100%;
  padding: 100px;
  margin-top: 5px;
}
.personal {
  width: 100%;
  text-align: center;
}
.personal img {
  border-radius: 50%;
  border: solid 5px #00324f;
  width: 400px;
  height: auto;
}
.personal-text {
  text-align: center;
  transform: translateY(20%);
}
.personal-text h2 {
  margin: 20px;
  font-weight: bold;
}
.personal-text p {
  font-size: 20px;
}
.technos img {
  height: 200px;
  width: 200px;
  margin: 50px;
}
.technos {
  text-align: center;
}
@media screen and (max-width: 1400px) {
  .apropos-container {
    display: block;
    width: 100%;
    padding: 50px;
    margin-top: 5px;
  }
  .personal img {
    border-radius: 50%;
    border: solid 5px #00324f;
    width: 200px;
    height: auto;
  }
  .personal-text {
    text-align: center;
    transform: translateY(20%);
  }
}
