.mes-projets-content {
  text-align: center;
  padding: 50px 15px;
  margin-top: 5px;
}
.mes-projets-content h3 {
  margin: 20px 0px !important;
}
.mes-projets-content .col {
  flex-grow: unset;
  margin: 15px 0;
}
.mes-projets-content .row {
  justify-content: center;
}
.card-img-top {
  height: 200px !important;
  width: 280px !important;
  margin: auto;
  margin-top: 10px;
}
.card p {
  line-height: 10px;
}
.col a {
  text-decoration: none;
  color: black;
}
.card-title {
  margin-bottom: 20px;
}
.mes-projets-content h3 {
  margin: 10px;
  margin-top: 20px;
}
.card:hover {
  box-shadow: 4px 2px 8px black;
  top: 20px;
  position: relative;
}

.overlay-effect {
  position: absolute;
  top: 0px;
  width: 288px;
  height: 398px;
  opacity: 0;
  background: linear-gradient(#00d0f5, white);
  z-index: 1;
  visibility: hidden;
  border-radius: 0.25rem;
}
.overlay-effect-mini {
  position: absolute;
  top: 0px;
  width: 287px;
  height: 294px;
  opacity: 0;
  background: linear-gradient(#00d0f5, white);
  z-index: 1;
  visibility: hidden;
  border-radius: 0.25rem;
}
.card:hover .overlay-effect {
  opacity: 0.4;
  visibility: visible;
  height: 370px;
}
.card:hover .overlay-effect-mini {
  opacity: 0.4;
  visibility: visible;
}
