/*--------------------------------------------------------------
    # Header
    --------------------------------------------------------------*/

.header-nav-background {
  padding-top: 65px;
  height: 190px;
  top: 0;
  right: 0;
  transition: all 0.5s;
  z-index: 997;
  background-image: url("/img/fond.png");
  background-size: cover;
  background-position: center;
  width: 100%;
  box-shadow: 4px 2px 8px #00d0f5;
}
.main-content {
  background-color: #f7f7f7;
}

@media screen and (min-width: 887px) and (max-width: 1250px) {
  .header-nav-background {
    padding-top: 110px;
  }
  .header-nav-background #logo-name {
    color: white;
    position: absolute;
    left: 112px;
  }
}
