#contact {
  background: #00324f;
  color: #fff;
  font-size: 14px;
  text-align: center;
  padding: 10px;
  padding-bottom: 30px;
}

#footer .copyright {
  text-align: center;
  margin: 10px;
}
.footer-contact a {
  text-decoration: none !important;
  color: white;
  font-size: 20px;
}
.footer-contact a:hover {
  color: #00d0f5;
  text-decoration: none;
}
.footer-contact {
  margin: 20px;
}
.footer-icons {
  font-size: 50px;
  margin-bottom: 30px;
}
.footer-icons svg {
  color: white;
  background-color: #00324f;
  margin: 20px;
}
.footer-icons svg:hover {
  color: #00d0f5;
  background-color: white;
  border-radius: 5px;
}
.footer-icons a {
  margin: 0;
  padding: 0;
}
