.accueil-main-content {
  height: auto;
  color: black;
  max-width: 100%;

  text-align: center;
}

.carousel-main {
  height: auto;
  padding: 20px 0px;
  margin-top: 5px;
}
.carousel-item {
  transition: transform 20s ease-in-out !important;
}
.carousel-control-next,
.carousel-control-prev,
.carousel-indicators {
  display: none !important;
}
.carousel-control-next,
.carousel-control-prev {
  width: 3% !important;
}
.carousel-indicators li {
  background-color: #00324f !important;
  border-radius: 50px;
}
.carousel-control-prev-icon,
.carousel-control-next-icon {
  background-color: #00324f !important;
  border-radius: 10px;
}
.accueil-content {
  padding: 50px;
}
.accueil-content .col {
  padding: 20px;

  margin: 0px 40px;
}
.accueil-content .row {
  text-align: center;
  justify-content: center;
}
.accueil-content h3 {
  font-weight: bold;
  margin: 10px;
  margin-bottom: 50px;
  background-color: #00324f;
  color: white;
  padding: 5px 0px;
  margin-bottom: 50px;
  border-radius: 10px;
}
.accueil-content p {
  font-size: 20px;
}
.accueil-content svg {
  position: relative;
  bottom: 4px;
  margin: 0px 10px;
}
